<template>
    <div class="p-20">
        <div class="filter-container" style="margin-bottom: 20px">
            <el-input class="filter-item" style="width: 300px;" placeholder="请输入交易商品名称"
                      @keyup.native.enter="typeQuery"
                      v-model="matchQuery.baseName">
            </el-input>
            <span style="margin-left: 20px;margin-right: 10px">状态：</span>
            <el-select clearable style="width: 150px" class="filter-item"
                       v-model="matchQuery.status">
                <el-option v-for="typeStatus in statusOptions" :key="typeStatus.key"
                           :label="typeStatus.status_name"
                           :value="typeStatus.key">
                </el-option>
            </el-select>
            <el-button class="filter-item" size="small" @click="typeQuery">查询</el-button>
            <el-button type="primary" @click="handleDialogShow('add')" size="small" v-if="permission.add">新增</el-button>
        </div>

        <el-table
                ref="singleTable"
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    v-for="item in tableHeader"
                    :key="item.prop"
                    :label="item.label"
                    :prop="item.prop"
                    :width="item.width"
                    :min-width="item.minWidth"
                    :align="item.align"
                    :formatter="item.formatter"
                    show-overflow-tooltip>
            </el-table-column>
            <el-table-column
                    fixed="right"
                    label="操作"
                    width="170">
                <template scope="scope" class="filter-operation">
                    <ul style="list-style: none;padding: 0px">
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showModifyDialog(scope.row.id)" v-if="permission.edit">修改
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-if="scope.row.status===1">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(2,scope.row.id,'启用')" v-if="permission.enable">启用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px" v-else-if="scope.row.status===2">
                            <el-button
                                    type="text" size="small"
                                    @click="enableDisable(1,scope.row.id,  '禁用')" v-if="permission.disable">禁用
                            </el-button>
                        </li>
                        <li style="float:left;width: 30px">
                            <el-button
                                    type="text" size="small"
                                    @click="showFeesDialog(scope.row.id)" v-if="permission.edit">修改手续费
                            </el-button>
                        </li>
                    </ul>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination @size-change="userPageSize" @current-change="userPage"
                           :current-page="matchQuery.page.pageNum"
                           :page-sizes="[10,20,30, 50]" :page-size="matchQuery.page.pageSize"
                           layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>

        <el-dialog title="添加法币类型匹配信息" :visible.sync="dialogVisible" @close="dialogClose">
            <el-form :model="matchData" :rules="ruleMatch" ref="matchData">
                <el-form-item :label-width="itemWidth" label="*序号" prop="sort">
                    <el-input-number class="input-short" v-model="matchData.sort" :min="0"
                                     :max="9999999"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*法币类型" prop="baseSeq">
                    <el-select v-model="matchData.baseSeq" placeholder="请选择">
                        <el-option
                                v-for="item in matchList"
                                :key="item.baseSeq"
                                :label="item.chineseName"
                                :value="item.baseSeq">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*交易商品类型" prop="tradeSeq">
                    <el-select v-model="matchData.tradeSeq" placeholder="请选择">
                        <el-option
                                v-for="item in goodsList"
                                :key="item.id"
                                :label="item.goodsName"
                                :value="item.id">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*单价小数位" prop="priceScale">
                    <el-input-number class="input-short" v-model="matchData.priceScale" :min="0"
                                     :max="8"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*数量小数位" prop="quantityScale">
                    <el-input-number class="input-short" v-model="matchData.quantityScale" :min="0"
                                     :max="8"
                                     auto-complete="off"></el-input-number>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*交易时间" prop="dealTime">
                    <el-input class="input-short" v-model="matchData.dealTime" placeholder="0/24/8:30-12:00,13:30-18:00"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*开盘价" prop="openPrice">
                    <el-input class="input-short" v-model="matchData.openPrice" placeholder="0.00000001~9999999"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*开盘时间" prop="openTime">
                    <el-input class="input-short" v-model="matchData.openTime" placeholder="年-月-日#小时:分钟"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最小挂单数量" prop="minSaleNum">
                    <el-input class="input-short" v-model="matchData.minSaleNum" placeholder="0.0001~9999999"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最小挂单单价" prop="minSalePrice">
                    <el-input class="input-short" v-model="matchData.minSalePrice" placeholder="0.00000001~9999999"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*最小挂单金额" prop="minSaleAmount">
                    <el-input class="input-short" v-model="matchData.minSaleAmount" placeholder="0.00000001~9999999"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*是否可以交易" prop="dealStop">
                    <el-switch
                            v-model="matchData.dealStop"
                            on-text=""
                            off-text=""
                            :on-value="2"
                            :off-value="1">
                    </el-switch>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="*节假日是否交易" prop="holidayStop">
                    <el-switch
                            v-model="matchData.holidayStop"
                            on-text=""
                            off-text=""
                            :on-value="2"
                            :off-value="1">
                    </el-switch>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="交易提醒-中文" prop="tip">
                    <el-input class="input-short" v-model="matchData.tip" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item :label-width="itemWidth" label="交易提醒-英文" prop="tipEn">
                    <el-input class="input-short" v-model="matchData.tipEn" type="textarea"
                              auto-complete="off"></el-input>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="commitAction">保 存</el-button>
            </div>
        </el-dialog>

        <el-dialog title="修改手续费率" :visible.sync="feesDialogVisible" @close="feesDialogClose">
            <span style="color: #FF0000;position:absolute;top:22px;left:140px;">0.01 = 1%</span>
            <el-form :model="fees" :rules="ruleFees" ref="fees">
                <el-form-item :label-width="itemWidth" label="等级[ 1 ]买入手续费：" prop="buyOne">
                    <el-input class="input-fee" v-model="fees.buyOne"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 1 ]卖出手续费：" prop="saleOne">
                    <el-input class="input-fee" v-model="fees.saleOne"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 2 ]买入手续费：" prop="buyTwo">
                    <el-input class="input-fee" v-model="fees.buyTwo"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 2 ]卖出手续费：" prop="saleTwo">
                    <el-input class="input-fee" v-model="fees.saleTwo"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 3 ]买入手续费：" prop="buyThree">
                    <el-input class="input-fee" v-model="fees.buyThree"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 3 ]卖出手续费：" prop="saleThree">
                    <el-input class="input-fee" v-model="fees.saleThree"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 4 ]买入手续费：" prop="buyFour">
                    <el-input class="input-fee" v-model="fees.buyFour"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 4 ]卖出手续费：" prop="saleFour">
                    <el-input class="input-fee" v-model="fees.saleFour"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 5 ]买入手续费：" prop="buyFive">
                    <el-input class="input-fee" v-model="fees.buyFive"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 5 ]卖出手续费：" prop="saleFive">
                    <el-input class="input-fee" v-model="fees.saleFive"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 6 ]买入手续费：" prop="buySix">
                    <el-input class="input-fee" v-model="fees.buySix"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 6 ]卖出手续费：" prop="saleSix">
                    <el-input class="input-fee" v-model="fees.saleSix"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 7 ]买入手续费：" prop="buySeven">
                    <el-input class="input-fee" v-model="fees.buySeven"
                              auto-complete="off"></el-input>

                </el-form-item>
                <el-form-item :label-width="itemWidth" label="等级[ 7 ]卖出手续费：" prop="saleSeven">
                    <el-input class="input-fee" v-model="fees.saleSeven"
                              auto-complete="off"></el-input>

                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="feesDialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="modifyFees">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>


<script>

    import fetch from '@/utils/fetch';
    import {virtualCoinAndGoods} from '@/api/virtual';

    import {validateEightDecimal, characterSize, validateDateTime, validateTimeTwoArea} from '@/utils/validate';

    const statusOptions = [
        {key: '', status_name: '全部状态'},
        {key: '1', status_name: '禁用'},
        {key: '2', status_name: '启用'},
    ];

    export default {
        data() {
            let validate = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请选择类型'));
                }
                callback();
            };
            let validateDealTime = (rule, value, callback) => {
                if (value === '0' || value === '24')
                    callback();
                if (value === '' || !validateTimeTwoArea(value)) {
                    callback(new Error('请输入交易时间，不交易填0，24小时交易填24,8:30-18:00,早上8点半到下午六点'));
                }
                callback();
            };
            let validateOpenPrice = (rule, value, callback) => {
                if (value === '' || !validateEightDecimal(value) || value * 1 > 9999999 || value * 1 < 0.00000001) {
                    callback(new Error('请输入数值，最大值9999999，最小值0.00000001'));
                }
                callback();
            };
            let validateContent = (rule, value, callback) => {
                if (characterSize(value) > 200) {
                    callback(new Error('最大字符数200'));
                }
                callback();
            };
            let validateFees = (rule, value, callback) => {
                if (value === '' || !validateEightDecimal(value)) {
                    callback(new Error('费率输入不合法'));
                }
                callback();
            };
            let validateOpenTime = (rule, value, callback) => {
                if (value === '0')
                    callback();
                if (value === '' || !validateDateTime(value)) {
                    callback(new Error('请输入开盘时间，0代表启用后立即开盘，自定义时间：年-月-日#小时:分钟）'));
                }
                callback();
            };
            return {
                itemWidth: '160px',
                dialogVisible: false,
                feesDialogVisible: false,
                tableData: [],
                statusOptions,
                action: '',
                tableHeader: [{
                    label: "序号",
                    prop: "sort",
                    width: 80
                }, {
                    label: "FID",
                    prop: "id",
                    width: 60
                }, {
                    label: "法币名称",
                    prop: "baseName",
                    width: 120
                }, {
                    label: "交易商品名称",
                    prop: "tradeName",
                    width: 180
                }, {
                    label: "交易时间",
                    prop: "dealTime",
                    width: 200
                }, {
                    label: "状态",
                    prop: "status",
                    width: 80,
                    formatter(row, column, value) {
                        return value === 1 ? '禁用' : (value === 2 ? '启用' : '');
                    }
                }, {
                    label: "数量小数位",
                    prop: "quantityScale",
                    width: 120
                }, {
                    label: "单价小数位",
                    prop: "priceScale",
                    width: 120
                }, {
                    label: "最小挂单数量",
                    prop: "minSaleNum",
                    width: 120
                }, {
                    label: "最小挂单单价",
                    prop: "minSalePrice",
                    width: 120,
                }, {
                    label: "最小挂单金额",
                    prop: "minSaleAmount",
                    width: 120
                }, {
                    label: "开盘价",
                    prop: "openPrice",
                    width: 120
                }, {
                    label: "开盘时间",
                    prop: "openTime",
                    width: 120
                }],
                matchData: {
                    id: '',
                    type: '2',
                    sort: '',
                    baseSeq: '',
                    tradeSeq: '',
                    priceScale: '',
                    quantityScale: '',
                    dealTime: '',
                    openTime: '',
                    openPrice: '',
                    minSaleNum: '',
                    minSalePrice: '',
                    minSaleAmount: '',
                    dealStop: '2',
                    holidayStop: '1',
                    tip: '',
                    tipEn: ''
                },
                ruleMatch: {
                    baseSeq: [{validator: validate, trigger: 'blur'}],
                    tradeSeq: [{validator: validate, trigger: 'blur'}],
                    dealTime: [{validator: validateDealTime, trigger: 'blur'}],
                    openPrice: [{validator: validateOpenPrice, trigger: 'blur'}],
                    openTime: [{validator: validateOpenTime, trigger: 'blur'}],
                    minSaleNum: [{validator: validateOpenPrice, trigger: 'blur'}],
                    minSalePrice: [{validator: validateOpenPrice, trigger: 'blur'}],
                    minSaleAmount: [{validator: validateOpenPrice, trigger: 'blur'}],
                    tip: [{validator: validateContent, trigger: 'blur'}],
                    tipEn: [{validator: validateContent, trigger: 'blur'}]
                },
                matchList: [],
                goodsList: [],
                matchQuery: {
                    baseName: '',
                    status: '',
                    page: {
                        pageNum: 1,
                        pageSize: 10
                    }
                },
                total: 0,
                fees: {
                    id: '',
                    tradeMappingSeq: '',
                    buyOne: '',
                    buyTwo: '',
                    buyThree: '',
                    buyFour: '',
                    buyFive: '',
                    buySix: '',
                    buySeven: '',
                    saleOne: '',
                    saleTwo: '',
                    saleThree: '',
                    saleFour: '',
                    saleFive: '',
                    saleSix: '',
                    saleSeven: '',
                },
                ruleFees: {
                    buyOne: [{validator: validateFees, trigger: 'blur'}],
                    buyTwo: [{validator: validateFees, trigger: 'blur'}],
                    buyThree: [{validator: validateFees, trigger: 'blur'}],
                    buyFour: [{validator: validateFees, trigger: 'blur'}],
                    buyFive: [{validator: validateFees, trigger: 'blur'}],
                    buySix: [{validator: validateFees, trigger: 'blur'}],
                    buySeven: [{validator: validateFees, trigger: 'blur'}],
                    saleOne: [{validator: validateFees, trigger: 'blur'}],
                    saleTwo: [{validator: validateFees, trigger: 'blur'}],
                    saleThree: [{validator: validateFees, trigger: 'blur'}],
                    saleFour: [{validator: validateFees, trigger: 'blur'}],
                    saleFive: [{validator: validateFees, trigger: 'blur'}],
                    saleSix: [{validator: validateFees, trigger: 'blur'}],
                    saleSeven: [{validator: validateFees, trigger: 'blur'}]
                }
            }
        },
        methods: {
            typeQuery() {
                this.getList();
            },
            handleDialogShow(action) {
                this.action = action;
                switch (action) {
                    case 'add':
                        this.dialogVisible = true;
                        break;
                    case 'update':
                        this.dialogVisible = true;
                        break;
                    case 'editFees':
                        this.feesDialogVisible = true;
                        break;
                }
            },
            dialogClose() {
                this.getList();
                this.resetMatch();
            },
            feesDialogClose() {
                this.resetFees();
            },
            resetFees() {
                let key;
                for (key in this.fees) {
                    switch (key) {
                        default:
                            this.fees[key] = '';
                    }
                }
            },
            resetMatch() {
                let key;
                for (key in this.matchData) {
                    switch (key) {
                        case 'type':
                        case 'dealStop':
                            this.matchData[key] = '2';
                            break;
                        case 'holidayStop':
                            this.matchData[key] = '1';
                            break;
                        default:
                            this.matchData[key] = '';
                    }
                }
            },
            setModel(data) {
                let key;
                for (key in this.matchData) {
                    this.matchData[key] = data[key] === null ? '' : data[key];
                }
            },
            getList() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademapping/2/query',
                        method: 'post',
                        data: _this.matchQuery
                    }).then(function (response) {
                        if (response.result) {
                            _this.tableData = response.data.data;
                            _this.total = response.data.page.count;
                        }
                    }).catch();
                })
            },
            create() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapping',
                        method: 'post',
                        data: _this.matchData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                        }
                        _this.getMessage(response.msg, response.result);
                    }).catch();
                })
            },
            commitAction() {
                let _this = this;
                this.$refs['matchData'].validate((valid) => {
                    if (valid) {
                        if (this.matchData.baseSeq === this.matchData.tradeSeq) {
                            this.$message.error('法币类型和交易币类型不能相同');
                            return;
                        }
                        _this.matchData.openTime = _this.matchData.openTime.replace('#', ' ');
                        console.log(_this.matchData.openTime);
                        switch (this.action) {
                            case 'add':
                                this.create();
                                break;
                            case 'update':
                                this.modifyMatchType();
                                break;
                        }
                    }
                });
            },
            getGoods() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/getgoodslist',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.goodsList = response.data;
                        }
                    }).catch();
                });
            },
            //翻页的按钮
            userPage(val) {
                this.matchQuery.page.pageNum = val;
                this.getList();
            },
            //获取每页多少条
            userPageSize(val) {
                this.matchQuery.page.pageSize = val;
                this.getList();
            },
            //启用禁用
            enableDisable(int, id, str) {
                this.$confirm('要' + str + '此法币匹配类型,是否继续', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    let url = 'admin/trademapping/' + id;
                    if (int === 1) {
                        this.enableDisableMatch(url + '/forbid');
                    } else if (int === 2) {
                        this.enableDisableMatch(url + '/enable');
                    }
                }).catch(() => {
                    this.$message.info('操作已取消');
                });
            },
            enableDisableMatch(url) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url,
                        method: 'put'
                    }).then(function (response) {
                        _this.getMessage(response.msg, response.result);
                        _this.getList();
                    }).catch();
                });
            },
            showModifyDialog(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapping/' + id,
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            _this.setModel(response.data);
                            _this.handleDialogShow('update');
                        }
                    }).catch();
                });
            },
            modifyMatchType() {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: 'admin/trademapping/' + _this.matchData.id,
                        method: 'put',
                        data: _this.matchData
                    }).then(function (response) {
                        if (response.result) {
                            _this.dialogVisible = false;
                        }
                        _this.getMessage(response.msg, response.result);
                        _this.getList();
                    }).catch();
                });
            },
            showFeesDialog(id) {
                let _this = this;
                return new Promise((resolve, reject) => {
                    fetch({
                        url: '/admin/trademapping/' + id + '/fees',
                        method: 'get'
                    }).then(function (response) {
                        if (response.result) {
                            if (response.data) {
                                _this.fees = response.data;
                            }
                            _this.fees.tradeMappingSeq = id;
                            _this.feesDialogVisible = true;
                        }
                    }).catch();
                });
            },
            modifyFees() {
                let _this = this;
                this.$refs['fees'].validate((valid) => {
                    if (valid) {
                        return new Promise((resolve, reject) => {
                            fetch({
                                url: 'admin/trademapping/fees',
                                method: 'post',
                                data: _this.fees
                            }).then(function (response) {
                                if (response.result) {
                                    _this.feesDialogVisible = false;
                                }
                                _this.getMessage(response.msg, response.result);
                            }).catch();
                        });
                    }
                });
            },
            getVirtual() {
                let _this = this;
                virtualCoinAndGoods([1, 3], 2).then(response => {
                    if (response.result) {
                        _this.matchList = response.data;
                    }
                })
            },
        },
        created() {
            this.getList();
            this.getGoods();
            this.getVirtual();
        }
    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .input-short {
        width: 300px;
    }

    .input-fee {
        width: 90px;
    }
</style>


